<template>
    <g :class="[active ? 'active' : '','button']" 
        @mousedown="active = true"
        @mouseup="active = false"
        @touchstart="active = true"
        @touchend="active = false"
        @click="action">
        <circle 
            :r="radius - 10"
            :cx="x" :cy="y" 
            :clip-path="[ position == 'top' ? 'url(#topButtonMask)' : 'url(#bottomButtonMask)']"/>
        <text x="50%" :y="topPosition" text-anchor="middle" alignment-baseline="central">{{ text }}</text>
    </g>
</template>
<script>
export default {
    name: "MaskedButton",
    props: [ "radius", "x", "y", "position", "text", "fill", "action"],
    data: function(){
        return {
            active: false
        }
    },
    computed:{
        "topPosition": function(){
            let center = this.radius / 4
            return (this.position == 'top') ? this.y - this.radius + center : this.y + this.radius - center
        }
    }
}
</script>

<style>
    .button,.button * {
        -webkit-touch-callout: none;
        -webkit-user-select: none;
        -khtml-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none;
        -webkit-tap-highlight-color: transparent;
        cursor: pointer;
    }
    .button text{
        font-size: 1.5rem;
        fill: #fff;
    }
    .button circle{
        fill:rgba(0, 0, 0, 0.2)
    }
    .button.active circle{
        fill: #f0f0f0
    }
    .button.active text{
        font-size: 1.5rem;
        fill:#03a9f4;
        text-shadow: 0px 0px 7px lighten(#029ae0f5, 20%);
    }
</style>

