<template>
    <svg class="progress" :width="width" :height="height" :viewBox="viewBox">
        
        <CounterDefs :path="path" :x="cx" :y="cy" :radius="RADIUS"/>
        
        <rect  fill="url(#background)" :width="width" :height="height"></rect>
        
        <g clip-path="url(#clipPath)">
            <rect  fill="#fff" :width="width" :height="height"></rect>
            <rect  fill="url(#forground)" :width="width" :height="height"></rect>
        </g>
        
        <!-- <circle :cx="cx" :cy="cy" :r="RADIUS" fill="white"/>  -->
        
        <transition 
            name="fade" 
            enter-active-class="fadeInUp" 
            leave-active-class="fadeOutUp"
            v-for="item in Array.from(Array(100), (_, x) => x)" :key="item">  
                <text class="progress__text animated"
                    v-if="count == item"
                    x="50%" y="50%" alignment-baseline="middle" 
                    text-anchor="middle">{{ tasbeeh.text }}</text>
        </transition>
        
        <transition  name="fadeIn" enter-active-class="fadeIn">
            <CompletedMessage class="animated" :radius="RADIUS" :x="cx" :y="cy" v-if="count == tasbeeh.times"/>
        </transition>
        
        <MaskedButton :radius="RADIUS" :x="cx" :y="cy" position="top" text="عودة" :action="back" ></MaskedButton>
        
        <MaskedButton :radius="RADIUS" :x="cx" :y="cy" position="bottom" text="أحسب" :action="increase" ></MaskedButton>
        
        <text id="counterValue" x="50%" :y="(cy - RADIUS) / 2" text-anchor="middle" alignment-baseline="central">{{ count }}</text>
        
        <circle class="progress__meter" :cx="cx" :cy="cy" :r="RADIUS" :stroke-width="strockWidth" />
        
        <circle class="progress__value" :style="{strokeDashoffset : dashoffset, strokeDasharray: CIRCUMFERENCE}" :cx="cx" :cy="cy" :r="RADIUS" :stroke-width="strockWidth" />
    </svg>
</template>
<script>
import "vue2-animate/dist/vue2-animate.min.css"
import MaskedButton from "../components/MaskedButton.vue"
import CounterDefs from "../components/CounterDefs.vue"
import CompletedMessage from "../components/CompletedMessage.vue"

export default {
    name: "Show",
    components: {
        MaskedButton,
        CounterDefs,
        CompletedMessage
    },
    data: function(){
        return {
            tasbeeh: this.$store.state.items[this.$route.params.id],
            CIRCUMFERENCE: null,
            RADIUS: null,
            width: null,
            height: null,
            cx: null,
            cy: null,
            viewBox: null,
            strockWidth: 14,
            count:this.$store.state.items[this.$route.params.id].count,
            value:0,
        }
    },
    computed: {
        "dashoffset" : function(){
            return this.CIRCUMFERENCE * ( 1 - (this.value / 100) )
        },
        "angle" : function(){
            return (this.value * 360) / 100
        },
        "path": function(){
            let step = this.radToDeg( Math.atan(this.cx / this.cy) )
            let lines = ""
            if(this.angle < step)
                lines = `L${ this.cx +( Math.tan( this.degToRad(this.angle) ) * this.cy) } 0` 
            else if(this.angle == step)
                lines = `L${ this.width } 0` 
            else if(this.angle > step && this.angle <= 180){
                let line = this.cy - ( this.cx * Math.tan( this.degToRad(90 - this.angle) ) )
                lines = `L${ this.width } 0 L${this.width} ${line}`
            }
            else if(this.angle > 180 - step && this.angle <= 180 + step){
                let line = this.cx + ( this.cy * Math.tan( this.degToRad(180 - this.angle) ) )
                lines = `L${ this.width } 0 L${this.width} ${this.height} L${line} ${this.height}`
            }
            else if(this.angle > 180 + step && this.angle < 360){
                let line = this.cy + ( this.cx * Math.tan( this.degToRad(270 - this.angle) ) )
                lines = `L${ this.width } 0 L${this.width} ${this.height} L0 ${this.height} L0 ${line}`
            }
            else if(this.angle == 360){
                lines = `L${ this.width } 0 L${this.width} ${this.height} L0 ${this.height} L0 0 L${this.cx} 0`
            }

            return [
                `M${this.cx} ${this.cy}`,
                `L${this.cx} 0`,
                `${lines}`,
                `Z`,
            ].join(' ');
        }
    },
    watch: {
        "count": function(){
            this.value = this.count * 100 / this.tasbeeh.times
        }
    },
    methods: {
        radToDeg: function(radians) {        
            return radians * 180 / Math.PI;
        },
        degToRad: function(degrees) {        
            return degrees * Math.PI / 180;
        },
        increase: function(){
            if(this.count < this.tasbeeh.times){
                this.count++
                this.$store.commit('increase', {
                    index: this.$route.params.id,
                    count: this.count
                })
            }
        },
        back: function(){
            this.$router.push({name: 'home'})
        }

    },
    beforeMount() {
        this.width = (window.innerWidth > 300) ? window.innerWidth : 300
        this.height = window.innerHeight
        this.viewBox = `0 0 ${this.width} ${this.height}`
        this.cx = this.width / 2
        this.cy = this.height / 2
        this.RADIUS = (this.cx < this.cy) ? this.cx - 50 : this.cy - 50
        this.CIRCUMFERENCE = 2 * Math.PI * this.RADIUS
        window.addEventListener("resize", (e) => {
            this.width = (e.target.innerWidth > 300) ? e.target.innerWidth : 300
            console.log(this.width);
            this.height = e.target.innerHeight
            this.viewBox = `0 0 ${this.width} ${this.height}`
            this.cx = this.width / 2
            this.cy = this.height / 2
            this.RADIUS = (this.cx < this.cy) ? this.cx - 50 : this.cy - 50
            this.CIRCUMFERENCE = 2 * Math.PI * this.RADIUS
            this.$forceUpdate()
        })
    },
    mounted(){
        this.value = this.count * 100 / this.tasbeeh.times
    }
}
</script>

<style>
    .progress {
        width:100%;
        height:100%;
        position:fixed;
        top:0;
        left:0;
        bottom:0;
        right:0;
    }
    .progress__meter,
    .progress__value {
        fill: none;
    }
    .progress__meter {
        stroke: #e6e6e6;
    }
    .progress__text {
        font-size: 4rem;
        transform-origin: 50%;
    }
    #counterValue{
        font-size: 2rem
    }
    .progress__text, #counterValue {
        fill:#03a9f4;
        -webkit-touch-callout: none;
        -webkit-user-select: none;
        -khtml-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none;
        -webkit-tap-highlight-color: transparent;
    }
    @media (max-width: 500px) {

        .progress__text { 
            font-size: 3em !important; 
        }
        #counterValue{
            font-size: 5rem
        }
    }
    @media (max-width: 420px) {

        .progress__text { 
            font-size: 2em !important; 
        }
    }
    .progress__value {
        transform: rotate(-90deg);
        transform-origin: 50%;
        stroke: #03a9f4;
        /* stroke-linecap: round;
        transition: stroke-dashoffset 0.4s ease-out; */
    }
    @keyframes fadeInUp {
        from {
            opacity: 0;
            transform: translate3d(0, 60px, 0);
        }

        to {
            opacity: 1;
            transform: none;
        }
    }
    @keyframes fadeOutUp {
        from {
            opacity: 1;
            transform: none;
        }
        to {
            opacity: 0;
            transform: translate3d(0, -60px, 0) scale(0);
        }
    }
</style>


