import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)
export default new Vuex.Store({
  state: {
    width: null,
    height: null,
    items: [
      {
        hadeeth: 'من قال حين يصبح وحين يمسي سبحان الله وبحمده مائة مرة غفرت خطاياه وإن كانت مثل زبد البحر',
        text: "سبحان الله وبحمده",
        times: 100,
        count: 0
      }
    ]
  },
  mutations: {
    "resize": (state, {
      innerWidth, innerHeight
    }) => {
      state.width = innerWidth
      state.height = innerHeight
    },
    "increase" : (state, {index,count}) => {
      state.items[index].count = count
    }
  },
  actions: {

  }
})
