<template>
    <defs>
        
        <clipPath id="clipPath">
            <path :d="path"/>
        </clipPath>

        <clipPath id="CompletedMessage">
            <rect :width="radius*2" :height="radius" :x="x - radius" :y="y - radius / 2"/>
        </clipPath>

        <clipPath id="topButtonMask">
            <rect :width="radius*2" :height="radius/2" :x="x - radius" :y="y - radius"/>
        </clipPath>

        <clipPath id="bottomButtonMask">
            <rect :width="radius*2" :height="radius/2" :x="x - radius" :y="y + radius / 2"/>
        </clipPath>

        <pattern id="background" patternUnits="userSpaceOnUse" width="100"  height="200" >
            <image xlink:href="../assets/background.png"  width="100"  height="200" x="0" y="0"/>
        </pattern>

        <pattern id="forground" patternUnits="userSpaceOnUse" width="100"  height="200" >
            <image xlink:href="../assets/forground.png"  width="100"  height="200" x="0" y="0"/>
        </pattern>

    </defs>
</template>

<script>
export default {
    name: "CounterDefs",
    props: ["radius", "path", "x", "y"],
    data: () => {
        return {}
    },
}
</script>

